import React from "react"
import { AppBar, Toolbar, Typography } from "@mui/material"
import { useTheme } from "@mui/material";
import { Box } from "@mui/material";
import { useEffect } from "react";
import { useState } from "react";
import { monitorFPS } from "../performance/fpsMonitor";
import { useMemo } from "react";
import { getPacageJSON, getPackageJSON } from "../misc";

const BottomBar = () => {

  const theme = useTheme();
  const [fps, setFps] = useState(0);

  useEffect(() => {
    const monitorHandler = _fps => {
      setFps(_fps.toFixed(0));
    };
    const { cancel } = monitorFPS(monitorHandler);
    return () => {
      cancel(monitorHandler);
    }
  }, []);

  const packageJSON = useMemo(() => {
    return getPackageJSON();
  }, []);

  return (
    <AppBar
      position="fixed"
      sx={{
        top: 'auto',
        bottom: 0,
        zIndex: theme.zIndex.drawer + 1,
        backgroundColor: 'var(--container-bg-3)'
      }}
    >
      <Toolbar
        style={{
          minHeight: 20
        }}
      >
        <Box
          flex={1}
        >
          <Typography
            fontSize={10}
            color="info"
          >{fps} FPS</Typography>
        </Box>
        <Typography
          fontSize={10}
          color="textDisabled"
        >
          {packageJSON.version}
        </Typography>
      </Toolbar>
    </AppBar>
  )
}

export default BottomBar;